.films{
    width: 100%;
    padding: 70px;
    height: auto;
}

.films_gallery {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    padding-left: 43px;
    gap: 50px;
}

.movie-card {
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
    width: 350;
    height: auto;
}

.movie-card:hover {
    filter: drop-shadow(0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06), 1px 4px 6px rgba(0, 0, 0, 0.16));
    transform: scale(1.1);
}

.poster-large{
    display: inline-block;
    width: 100%;
    height: 500px;
}

.gallery__image {
    width: 100%;
    height: 100%;
}

.info {
    padding: 15px 0px;
}

.card-movie-title {
    padding-left: 7px;
}

.info-items {
    display: flex;
    font-size: 12px;
    text-align: center;
    gap: 5px;
    padding: 7px;
    font-weight: bold;
    color: #FF6B08;
}
    



